//
.icon-arrow {
    display: inline-block;
    width: 30px;
    height: 50px;
    stroke-width: 0;
    stroke: currentColor;
    fill: none;
    color: #23a6d5;
}

.icon {
    display: inline-block;
    width: 1.5em;
    height: 1.5em;
    stroke-width: 0;
    stroke: currentColor;
    fill: currentColor;
}

// buttons
.btn {
    text-decoration: none;
    background: #fff;
    color: #333;
    text-transform: uppercase;
    letter-spacing: 2px;
    display: inline-block;
    font-weight: 700;
    border-radius: 5px;
    box-shadow: 0 5px 15px 0 rgb(0 0 0 / 15%);
    transition: transform .3s;
    outline: none;
    border: 0;
    cursor: pointer;
}

.btn:hover {
    transform: translateY(-3px);
}

.btn--theme {
    background: #0071bc;
    color: #fff;
}

.btn--size {
    padding: .5rem 2rem;
    font-size: .8rem;

    @include breakpoint(desktop) {
        font-size: 1rem;
    }
    @include breakpoint(large-desktop) {
        font-size: 1.125rem;
    }
}